<template>
	<div class="main-content">

		<!-- 条件查询 -->
		<div v-if="showFlag">
			<el-form :inline="true" :model="searchForm" class="form-content">
				<el-row :gutter="20" class="slt" :style="{justifyContent:contents.searchBoxPosition=='1'?'flex-start':contents.searchBoxPosition=='2'?'center':'flex-end'}">

					<el-form-item :label="contents.inputTitle == 1 ? '订单号' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.shangpinOrderUuidNumber" placeholder="订单号" clearable></el-input>
					</el-form-item>

					<el-form-item :label="contents.inputTitle == 1 ? '订单类型' : ''">
						<el-select v-model="searchForm.shangpinOrderTypes" placeholder="请选择订单类型">
							<el-option label="=-请选择-=" value=""></el-option>
							<el-option v-for="(item,index) in shangpinOrderTypesSelectSearch" v-bind:key="index" :label="item.indexName"
							 :value="item.codeIndex">
								<!--lable是要展示的名称-->
								<!--value是值-->
							</el-option>
						</el-select>
					</el-form-item>



					<el-form-item :label="contents.inputTitle == 1 ? '采购员姓名' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.caigouyuanName" placeholder="采购员姓名" clearable></el-input>
					</el-form-item>

					<el-form-item :label="contents.inputTitle == 1 ? '商品名称' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.shangpinName" placeholder="商品名称" clearable></el-input>
					</el-form-item>


					<el-form-item :label="contents.inputTitle == 1 ? '商品类型' : ''">
						<el-select v-model="searchForm.shangpinTypes" placeholder="请选择商品类型">
							<el-option label="=-请选择-=" value=""></el-option>
							<el-option v-for="(item,index) in shangpinTypesSelectSearch" v-bind:key="index" :label="item.indexName" :value="item.codeIndex">
								<!--lable是要展示的名称-->
								<!--value是值-->
							</el-option>
						</el-select>
					</el-form-item>

					<el-form-item :label="contents.inputTitle == 1 ? '用户姓名' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.yonghuName" placeholder="用户姓名" clearable></el-input>
					</el-form-item>


					<el-form-item>
						<el-button type="success" @click="search()">查询<i class="el-icon-search el-icon--right" /></el-button>
					</el-form-item>
				</el-row>
				<el-row class="ad" :style="{justifyContent:contents.btnAdAllBoxPosition=='1'?'flex-start':contents.btnAdAllBoxPosition=='2'?'center':'flex-end'}">
					<el-form-item>
						<el-button v-if="isAuth('shangpinOrder','新增')" type="success" icon="el-icon-plus" @click="addOrUpdateHandler()">新增</el-button>
						&nbsp;
						<el-button v-if="isAuth('shangpinOrder','删除')" :disabled="dataListSelections.length <= 0" type="danger" icon="el-icon-delete"
						 @click="deleteHandler()">删除</el-button>
						&nbsp;
						<el-button v-if="isAuth('shangpinOrder','报表')" type="success" icon="el-icon-pie-chart" @click="chartDialog()">报表</el-button>
						&nbsp;
						<a style="text-decoration:none" class="el-button el-button--success" v-if="isAuth('shangpinOrder','导入导出')" icon="el-icon-download"
						 href="http://localhost:8080/xiaoqutuangou/upload/shangpinOrderMuBan.xls">批量导入商品订单数据模板</a>
						&nbsp;
						<el-upload v-if="isAuth('shangpinOrder','导入导出')" style="display: inline-block" :action="getActionUrl" :on-success="shangpinOrderUploadSuccess"
						 :on-error="shangpinOrderUploadError" :show-file-list=false>
							<el-button v-if="isAuth('shangpinOrder','导入导出')" type="success" icon="el-icon-upload2">批量导入商品订单数据</el-button>
						</el-upload>
						&nbsp;
						<!-- 导出excel -->
						<download-excel v-if="isAuth('shangpinOrder','导入导出')" style="display: inline-block" class="export-excel-wrapper"
						 :data="dataList" :fields="json_fields" name="shangpinOrder.xls">
							<!-- 导出excel -->
							<el-button type="success" icon="el-icon-download">导出</el-button>
						</download-excel>
						&nbsp;
					</el-form-item>
				</el-row>
			</el-form>
			<div class="table-content">
				<el-table class="tables" :size="contents.tableSize" :show-header="contents.tableShowHeader" :header-row-style="headerRowStyle"
				 :header-cell-style="headerCellStyle" :border="contents.tableBorder" :fit="contents.tableFit" :stripe="contents.tableStripe"
				 :row-style="rowStyle" :cell-style="cellStyle" :style="{width: '100%',fontSize:contents.tableContentFontSize,color:contents.tableContentFontColor}"
				 v-if="isAuth('shangpinOrder','查看')" :data="dataList" v-loading="dataListLoading" @selection-change="selectionChangeHandler">
					<el-table-column v-if="contents.tableSelection" type="selection" header-align="center" align="center" width="50">
					</el-table-column>
					<el-table-column label="索引" v-if="contents.tableIndex" type="index" width="50" />
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="addressName" header-align="center"
					 label="收货人">
						<template slot-scope="scope">
							{{scope.row.addressName}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="addressPhone" header-align="center"
					 label="电话">
						<template slot-scope="scope">
							{{scope.row.addressPhone}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="addressDizhi" header-align="center"
					 label="地址">
						<template slot-scope="scope">
							{{scope.row.addressDizhi}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinName" header-align="center"
					 label="商品名称">
						<template slot-scope="scope">
							{{scope.row.shangpinName}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinPhoto" header-align="center"
					 width="200" label="商品照片">
						<template slot-scope="scope">
							<div v-if="scope.row.shangpinPhoto">
								<img :src="scope.row.shangpinPhoto" width="100" height="100">
							</div>
							<div v-else>无图片</div>
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinTypes" header-align="center"
					 label="商品类型">
						<template slot-scope="scope">
							{{scope.row.shangpinValue}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="yonghuName" header-align="center"
					 label="用户姓名">
						<template slot-scope="scope">
							{{scope.row.yonghuName}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="caigouyuanName"
					 header-align="center" label="采购员姓名">
						<template slot-scope="scope">
							{{scope.row.caigouyuanName}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="caigouyuanPhone"
					 header-align="center" label="采购员联系方式">
						<template slot-scope="scope">
							{{scope.row.caigouyuanPhone}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinOrderUuidNumber"
					 header-align="center" label="订单号">
						<template slot-scope="scope">
							{{scope.row.shangpinOrderUuidNumber}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="buyNumber" header-align="center"
					 label="购买数量">
						<template slot-scope="scope">
							{{scope.row.buyNumber}}
						</template>
					</el-table-column>

					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinOrderTruePrice"
					 header-align="center" label="实付价格">
						<template slot-scope="scope">
							{{scope.row.shangpinOrderTruePrice}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinOrderTypes"
					 header-align="center" label="订单类型">
						<template slot-scope="scope">
							{{scope.row.shangpinOrderValue}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="shangpinOrderPaymentTypes"
					 header-align="center" label="支付类型">
						<template slot-scope="scope">
							{{scope.row.shangpinOrderPaymentValue}}
						</template>
					</el-table-column>

					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="insertTime" header-align="center"
					 label="订单创建时间">
						<template slot-scope="scope">
							{{scope.row.insertTime}}
						</template>
					</el-table-column>

					<el-table-column width="300" :align="contents.tableAlign" header-align="center" label="操作">
						<template slot-scope="scope">
							<el-button v-if="isAuth('shangpinOrder','查看')" type="success" icon="el-icon-tickets" size="mini" @click="addOrUpdateHandler(scope.row.id,'info')">详情</el-button>
							<el-button v-if="isAuth('shangpinOrder','修改')" type="primary" icon="el-icon-edit" size="mini" @click="addOrUpdateHandler(scope.row.id)">修改</el-button>

							<el-button v-if="isAuth('shangpinOrder','删除')" type="danger" icon="el-icon-delete" size="mini" @click="deleteHandler(scope.row.id)">删除</el-button>
							<el-button v-if="isAuth('shangpinOrder','订单') && scope.row.shangpinOrderTypes == 1 && sessionTable=='caigouyuan' && userId==scope.row.caigouyuanId"
							 type="primary" icon="el-icon-sold-out" size="mini" @click="refund(scope.row.id)">退款</el-button>
							<el-button v-if="isAuth('shangpinOrder','订单') && scope.row.shangpinOrderTypes == 1 && sessionTable=='users'"
							 type="primary" icon="el-icon-reading" size="mini" @click="opdenDeliver(scope.row.id)">分配采购员</el-button>


							<el-button v-if="isAuth('shangpinOrder','订单') && scope.row.shangpinOrderTypes == 3 && sessionTable=='caigouyuan' && userId==scope.row.caigouyuanId"
							 type="primary" icon="el-icon-reading" size="mini" @click="receiving(scope.row.id)">已完成</el-button>

							<el-button v-if="isAuth('shangpinOrder','订单') && scope.row.shangpinOrderTypes == 4 && sessionTable=='yonghu' && userId==scope.row.yonghuId"
							 type="primary" icon="el-icon-reading" size="mini" @click="openCommentModel(scope.row.id)">评价</el-button>



						</template>
					</el-table-column>
				</el-table>
				<el-pagination clsss="pages" :layout="layouts" @size-change="sizeChangeHandle" @current-change="currentChangeHandle"
				 :current-page="pageIndex" :page-sizes="[10, 20, 50, 100]" :page-size="Number(contents.pageEachNum)" :total="totalPage"
				 :small="contents.pageStyle" class="pagination-content" :background="contents.pageBtnBG" :style="{textAlign:contents.pagePosition==1?'left':contents.pagePosition==2?'center':'right'}"></el-pagination>
			</div>
		</div>
		<!-- 添加/修改页面  将父组件的search方法传递给子组件-->
		<add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

		<!-- 评价 -->
		<el-dialog title="评价" :visible.sync="commentbackVisible" width="30%">
			<span>评价内容</span>
			<el-input type="textarea" v-model="commentbackContent"></el-input>
			<span slot="footer" class="dialog-footer">
				<el-button @click="commentbackVisible = false">取 消</el-button>
				<el-button type="primary" @click="commentback()">确 定</el-button>
			</span>
		</el-dialog>

		<el-dialog title="统计报表" :visible.sync="chartVisiable" width="auto">
			<el-date-picker v-model="echartsDate" type="year" placeholder="选择年">
			</el-date-picker>
			<el-button @click="chartDialog()">查询</el-button>
			<div id="statistic" style="width:100%;height:600px;"></div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="chartVisiable = false">关闭</el-button>
			</span>
		</el-dialog>


		<el-dialog title="分配采购员" :visible.sync="dialogFormVisible">
			<el-form :model="fahuoDate">
				<el-select v-model="fahuoDate.caigouyuanId" placeholder="请选择采购员">
					<el-option v-for="(item,index) in caigouyuanSelectSearch" v-bind:key="index" :label="item.caigouyuanName" :value="item.id">
						<!--lable是要展示的名称-->
						<!--value是值-->
					</el-option>
				</el-select>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="deliver">确 定</el-button>
			</div>
		</el-dialog>

	</div>
</template>
<script>
	import AddOrUpdate from "./add-or-update";
	import styleJs from "../../../utils/style.js";
	import utilsJs from "../../../utils/utils.js";

	export default {
		data() {
			return {
				searchForm: {
					key: ""
				},
				sessionTable: "", //登录账户所在表名
				role: "", //权限
				userId: "", //当前登录人的id
				//级联表下拉框搜索条件
				shangpinTypesSelectSearch: [],
				//当前表下拉框搜索条件
				shangpinOrderTypesSelectSearch: [],
				form: {
					id: null,
					shangpinOrderUuidNumber: null,
					addressId: null,
					shangpinId: null,
					buyNumber: null,
					shangpinOrderTruePrice: null,
					caigouyuanId: null,
					yonghuId: null,
					shangpinOrderTypes: null,
					shangpinOrderPaymentTypes: null,
					insertTime: null,
					createTime: null,
				},
				dataList: [],
				pageIndex: 1,
				pageSize: 10,
				totalPage: 0,
				dataListLoading: false,
				dataListSelections: [],
				showFlag: true,
				sfshVisiable: false,
				shForm: {},
				chartVisiable: false,
				echartsDate: new Date(), //echarts的时间查询字段
				addOrUpdateFlag: false,
				contents: null,
				layouts: '',

				fahuoDate: {
					id: null,
					caigouyuanId: null
				},
				caigouyuanSelectSearch: [],
				dialogFormVisible: false,

				//导出excel
				json_fields: {
					//级联表字段
					'收货人': 'addressName',
					'电话': 'addressPhone',
					'地址': 'addressDizhi',
					'是否默认地址': 'isdefaultTypes',
					'采购员姓名': 'caigouyuanName',
					'采购员手机号': 'caigouyuanPhone',
					'采购员身份证号': 'caigouyuanIdNumber',
					'采购员头像': 'caigouyuanPhoto',
					'电子邮箱': 'caigouyuanEmail',
					'商品名称': 'shangpinName',
					'商品编号': 'shangpinUuidNumber',
					'商品照片': 'shangpinPhoto',
					'商品类型': 'shangpinTypes',
					'商品库存': 'shangpinKucunNumber',
					'商品原价': 'shangpinOldMoney',
					'现价': 'shangpinNewMoney',
					'点击次数': 'shangpinClicknum',
					'是否上架': 'shangxiaTypes',
					'用户姓名': 'yonghuName',
					'用户手机号': 'yonghuPhone',
					'用户身份证号': 'yonghuIdNumber',
					'用户头像': 'yonghuPhoto',
					'电子邮箱': 'yonghuEmail',
					'余额': 'newMoney',
					//本表字段
					'订单号': "shangpinOrderUuidNumber",
					'购买数量': "buyNumber",
					'实付价格': "shangpinOrderTruePrice",
					'订单类型': "shangpinOrderTypes",
					'支付类型': "shangpinOrderPaymentTypes",
					'订单创建时间': "insertTime",
				},

				//评价
				//操作数据id
				commentbackId: null,
				//评价内容
				commentbackContent: null,
				//模态框状态
				commentbackVisible: false,
				//评分
				shangpinCommentbackPingfenNumber: 0,
			};
		},
		created() {
			this.contents = styleJs.listStyle();
			this.init();
			this.getDataList();
			this.contentStyleChange()
		},
		mounted() {
			//获取当前登录用户的信息
			this.sessionTable = this.$storage.get("sessionTable");
			this.role = this.$storage.get("role");
			this.userId = this.$storage.get("userId");

		},
		filters: {
			htmlfilter: function(val) {
				return val.replace(/<[^>]*>/g).replace(/undefined/g, '');
			}
		},
		components: {
			AddOrUpdate,
		},
		computed: {
			getActionUrl: function() {
				return `/` + this.$base.name + `/file/upload`;
			}
		},
		methods: {
			chartDialog() {
				let _this = this;
				let params = {
					dateFormat: "%Y", //%Y-%m
					riqi: _this.echartsDate.getFullYear(),
					// riqi :_this.echartsDate.getFullYear()+"-"+(_this.echartsDate.getMonth() + 1 < 10 ? '0' + (_this.echartsDate.getMonth() + 1) : _this.echartsDate.getMonth() + 1),
					thisTable: { //当前表
						tableName: 'shangpin_order', //当前表表名,
						sumColum: 'shangpin_order_number', //求和字段
						date: 'insert_time', //分组日期字段
						// string : 'shangpin_order_name',//分组字符串字段
						// types : 'shangpin_order_types',//分组下拉框字段
					},
					// joinTable : {//级联表（可以不存在）
					//     tableName :'yonghu',//级联表表名
					//     // date : 'insert_time',//分组日期字段
					//     string : 'yonghu_name',//分组字符串字段
					//     // types : 'yonghu_types',//分组下拉框字段
					// }
				}
				_this.chartVisiable = true;
				_this.$nextTick(() => {
					var statistic = this.$echarts.init(document.getElementById("statistic"), 'macarons');
					this.$http({
						url: "barSum",
						method: "get",
						params: params
					}).then(({
						data
					}) => {
						if (data && data.code === 0) {

							//柱状图 求和 已成功使用
							//start
							let yAxisName = "数值"; //根据查询数据具体改(单列要改,多列不改)
							let xAxisName = "月份";
							let series = []; //具体数据值
							data.data.yAxis.forEach(function(item, index) {
								let tempMap = {};
								// tempMap.name= ["数值"];//根据查询数据具体改(单列要改,多列不改)
								tempMap.name = data.data.legend[index];
								tempMap.type = 'bar';
								tempMap.data = item;
								series.push(tempMap);

							})

							var option = {
								tooltip: {
									trigger: 'axis',
									axisPointer: {
										type: 'cross',
										crossStyle: {
											color: '#999'
										}
									}
								},
								toolbox: {
									feature: {
										// dataView: { show: true, readOnly: false },  // 数据查看
										magicType: {
											show: true,
											type: ['line', 'bar']
										}, //切换图形展示方式
										// restore: { show: true }, // 刷新
										saveAsImage: {
											show: true
										} //保存
									}
								},
								legend: {
									data: data.data.legend //标题  可以点击导致某一列数据消失
								},
								xAxis: [{
									type: 'category',
									axisLabel: {
										interval: 0
									},
									name: xAxisName,
									data: data.data.xAxis,
									axisPointer: {
										type: 'shadow'
									}
								}],
								yAxis: [{
									type: 'value', //不能改
									name: yAxisName, //y轴单位
									axisLabel: {
										formatter: '{value}' // 后缀
									}
								}],
								series: series //具体数据
							};
							// 使用刚指定的配置项和数据显示图表。
							statistic.setOption(option, true);
							//根据窗口的大小变动图表
							window.onresize = function() {
								statistic.resize();
							};
							//end
						} else {
							this.$message({
								message: "报表未查询到数据",
								type: "success",
								duration: 1500,
								onClose: () => {
									this.search();
								}
							});
						}
					});
				});
				////饼状图
				//_this.chartVisiable = true;
				// this.$nextTick(()=>{
				//     var statistic = this.$echarts.init(document.getElementById("statistic"),'macarons');
				//     let params = {
				//         tableName: "shangpin_order",
				//         groupColumn: "shangpin_order_types",
				//     }
				//     this.$http({
				//         url: "newSelectGroupCount",
				//         method: "get",
				//         params: params
				//     }).then(({data}) => {
				//         if (data && data.code === 0) {
				//             let res = data.data;
				//             let xAxis = [];
				//             let yAxis = [];
				//             let pArray = []
				//             var option = {};
				//             for(let i=0;i<res.length;i++){
				//                 xAxis.push(res[i].name);
				//                 yAxis.push(res[i].value);
				//                 pArray.push({
				//                     value: res[i].value,
				//                     name: res[i].name
				//                 })
