			height: 35px;
			color: rgba(4, 4, 4, 1);
			font-size: 1px;
			border-width: 0px;
			border-style: solid;
			border-color: rgba(1, 1, 1, 1);
			border-radius: 4px;
			background-color: rgba(253, 228, 79, 1);
		}

		& /deep/ .el-button--primary {
			height: 35px;
			color: rgba(8, 8, 8, 1);
			font-size: 1px;
			border-width: 0px;
			border-style: solid;
			border-color: rgba(1, 1, 1, 1);
			border-radius: 4px;
			background-color: rgba(240, 125, 48, 1);
		}

		& /deep/ .el-button--danger {
			height: 35px;
			color: rgba(8, 8, 8, 1);
			font-size: 1px;
			border-width: 0px;
			border-style: solid;
			border-color: rgba(1, 1, 1, 1);
			border-radius: 4px;
